@import '../../../scss/variables';

.list-booking {

    .custom-card {

        &>.card-header {
            padding: 0rem 1.5rem;
        }
    }

    .header {
        @media only screen and (min-width: 425px) {
            &__custom-date-input {
                width: 124px;
            }
        }

        &__custom-date-input {
            &>.form-control {
                background-color: transparent !important;
                color: $gray-700;
            }

            .form-control {
                text-align: center;
            }
        }

        &__status {
            display: inline-table;
            margin: 0 auto;

            &__active {
                border-bottom: 4px solid #5292B0 !important;
                color: $primary;
            }

            &__item {
                height: 54px;
                border-bottom: 4px solid #ffffff !important;
                background-color: transparent !important;
                padding-bottom: 2px !important;

                &--active {
                    border-bottom: 4px solid #5292B0 !important;
                    color: $primary;
                }
            }
        }

        &__btn-filter {
            height: 44px;
            background-color: $accent1_10 !important;

            &__content {
                color: $gray-700;
            }
        }

        &__dropdown {
            &>.btn-secondary {
                box-shadow: none;
                color: $gray-700;
                background-color: transparent;

                &>.fas,
                .ni {
                    color: $gray-700;
                }
            }
        }
    }

    .table {
        &__header {
            &__checkbox {
                width: 75px;
            }

            &__w-95 {
                width: 95px;
            }
        }
    }
}

.booking {
    &__selected {
        background-color: rgba(77, 154, 191, 0.1);
    }
}