@import '../../scss/variables';

.sidebar {
    height: calc(100% - 112px);
    min-width: 320px;
    position: fixed !important;
    top: 0;
    right: 0;
    z-index: 10;
    transition: 0.6s;
    background-color: #ffffff;
    border: 1px solid #c8ced3;
    overflow-x: hidden;
    margin-top: 60px;
    border: 0px;
    border-radius: 5px;
    box-shadow: -3px 3px 10px 0px rgba(194, 194, 194, 0.5);

    &__wrapper {
        position: relative;

        .sidebar-show {
            margin-right: 0;
        }

        .sidebar-hide {
            margin-right: -800px;
        }
    }

    &__main {
        width: 300px;
        height: 100%;
        display: flex;
        flex-direction: column;

        .preview {
            color: $primary;
            padding: 20px;
            font-size: 14px;

            .label-highlight {
                text-transform: uppercase;
                color: $primary;
            }

            .color-light {
                opacity: 0.6;
            }

            .time {
                position: relative;
                padding-left: 15px;
                color: $orange;
                border-left: 5px solid $primary;
            }

            .duration {

                .value {
                    line-height: 1;
                    font-size: 22px;
                    color: $primary;
                }

                .label {
                    font-size: 12px;
                    font-weight: 100;
                    color: $primary;
                }
            }

            .space {
                img {
                    max-height: 200px;
                    width: 100%;
                    border: 2px solid $altText1;
                    border-radius: 10px;
                    object-fit: cover;
                }
            }
        }
    }

    &__icon {
        position: fixed;
        right: 0;

        &:hover {
            color: $red;
            border-radius: 3px;
        }
    }

    &__footer {
        margin-bottom: 20px;
        margin-top: auto;
        margin-left: auto;
        margin-right: auto;

        .btn:not(:last-child) {
            margin-right: 0;
            margin-bottom: 0.5rem;
        }

        .btn+.btn {
            margin-left: 0;
        }
    }
}